import React from 'react';
import './NavigationBar.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';

const navigationBar = (props) =>{
    return(
        <>
        <button className="toggle" onClick={()=>{$('.toggle').toggleClass('active'); $('.overlay').toggleClass('active'); $('.navigation').toggleClass('active')}}></button>
        <div className="overlay"></div>
        <nav className="navigation">
            <button><strong>About Me</strong></button>
            <button><strong>Skill Set</strong></button>
            <button><strong>Work</strong></button>
            <button><strong>Contact Me</strong></button>
        </nav>
        
        </>
    );
}

export default navigationBar;